.page section {
    height: auto;
    min-height: 100vh;
    /* max-height: 2000px; */
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
}
.page section:nth-child(odd) {
    background: var(--bg);
    color: var(--word);
}
.page section:nth-child(even) {
    background: var(--word);
    color: var(--bg);
}
.page section:last-child {
    height: 20rem;
    min-height: 20rem;
    max-height: 2000px;
}

.intro {
    position: relative;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}
.intro :global(.svgTitle) {
    transform: scale(0.5);
}

.iframeWrapper {
    position: relative;
    padding-bottom: 56.25%;
    overflow: hidden;
}
.iframeWrapper iframe {
    border: none;
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
}

.project {
    display: flex;
    justify-content: center;
    align-items: center;

    min-height: 10rem;
    height: 10rem;
    background-size: cover;
    background-position: top;
}
.project button {
    color: #333 !important;
    background: rgba(255, 255, 255, 0.5) !important;
}
.timeperiod {
    position: relative;
}
.timeperiod > *:nth-child(even) {
    margin-left: 0.5rem;
}
